<template>
  <div id="index">
    <div class="i-map">
      <img alt="无锡" src="../assets/img/0.png" class="animated" id="img">
    </div>
    <canvas id="canvas"></canvas>
  </div>
</template>

<script>
  import { toolMixins } from '../mixins/tools'
  import { gaussBlur } from '../utils/blur'

  export default {
    name: 'index',
    mixins: [toolMixins],
    methods: {
      blur () {
        let _this = this
        let img = document.getElementById('img')
        let image = new window.Image()
        image.src = img.getAttribute('src')
        image.onload = function () {
          let canvas = document.getElementById('canvas')
          let width = image.width / 1.1
          let height = image.height / 1.1

          canvas.style.width = canvas.width = width
          canvas.style.height = canvas.height = height

          let context = canvas.getContext('2d')
          context.drawImage(image, 0, 0, width, height)

          let canvasData = context.getImageData(0, 0, canvas.width, canvas.height)
          // 开始
          let tempData = gaussBlur(canvasData, 80)
          let data = new ImageData(tempData, width, height)
          context.putImageData(data, 0, 0)
          _this.addClass(canvas, 'animated')
        }
      }
    },
    mounted () {
      let {grayCircle, circle} = {
        grayCircle: document.querySelector('#grayCircle'),
        circle: document.querySelector('#circle')
      }
      this.removeClass(grayCircle, 'rotate36')
      this.addClass(circle, 'paused')
      this.removeClass(circle, 'animated')
      // 初始化
      this.$nextTick(() => {
        this.blur()
      })
    }
  }
</script>

<style lang="less">
  @import "../assets/css/base";

  #index {
    width: 100%;
    height: 100%;
    position: absolute;
    canvas {
      .middle;
      opacity: 0;
      &.animated {
        animation: fadeIn 1s ease-in-out 1s forwards;
      }
    }
  }
  .i-map {
    .middle;
    width: 260px;
    height: 260px;
    z-index: 0;
    > img {
      display: block;
      width: 100%;
      opacity: 0;
      transform: scale(0);
      &.animated {
        animation: rollIn 2s ease-in-out forwards;
      }
    }
  }
</style>



