<template>
  <div id="app" :style="'background-image:url(' + bg + ')'">
    <div class="i-banner animated" id="banner" :style="'background-image:url(' + banner + ')'"></div>
    <div class="i-main">
      <div class="i-circle animated" :style="'background-image:url(' + waiquan + ')'" id="circle"></div>
      <div class="i-blue__circle animated" id="blueCircle"></div>
      <div class="i-black__circle360 animated" :style="'background-image:url(' + black + ')'" id="blackCircle"></div>
      <div class="i-white__circle animated" id="whiteCircle"></div>
      <div class="i-lightblue__circle animated" id="lightCircle"></div>
      <div class="i-gray__circle animated" id="grayCircle"></div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
  import { toolMixins } from './mixins/tools'

  export default {
    name: 'app',
    mixins: [toolMixins],
    data () {
      return {
        banner: require('./assets/img/banner.png'),
        black: require('./assets/img/black_circle360.png'),
        bg: require('./assets/img/bg.png'),
        waiquan: require('./assets/img/waiquan.png')
      }
    },
    methods: {
      animationend () {
        let grayCircle = document.querySelector('#grayCircle')
        this.removeClass(grayCircle, 'animated')
      }
    },
    mounted () {
      console.log(this.bg)
      let [grayCircle, _this] = [document.querySelector('#grayCircle'), this]
      grayCircle.addEventListener('webkitAnimationEnd', function () {
        _this.animationend()
      })
      grayCircle.addEventListener('animationend', function () {
        _this.animationend()
      })
    }
  }
</script>

<style lang="less">
  @import "assets/css/reset";
  @import "assets/css/base";

  #app {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100% 100%;
  }

  .i-banner {
    .middle;
    width: 1030px;
    height: 1030px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 1030px 870px;
    &.animated {
      opacity: 0;
      animation: fadeIn 1s linear 1s forwards;
    }
  }
  .i-main {
    .middle;
    margin-left: -20px;
    margin-top: 10px;
    z-index: 100;
    .i-circle {
      .middle;
      background-position: center center;
      background-repeat: no-repeat;
      width: 511px;
      height: 511px;
      background-size: 511px 508px;
      &.animated {
        opacity: 0;
        transform: translateY(100%) scale(2.0);
        animation: fadeScaleIn 1s ease-in-out forwards, rotate360 45s linear 2s infinite;
      }
      &.paused {
        animation-play-state: paused;
      }
    }
    .i-blue__circle {
      .middle;
      background: url("./assets/img/blue_circle.png") center 22px no-repeat;
      width: 363px;
      height: 369px;
      background-size: 363px 347px;
      &.animated {
        opacity: 0;
        transform: translateY(100%) scale(2.0);
        animation: fadeScaleIn 1s ease-in-out 500ms forwards, rotate360R 20s linear 2s infinite;
      }
    }
    .i-black__circle360 {
      .middle;
      background-position: center center;
      background-repeat: no-repeat;
      width: 328px;
      height: 328px;
      margin-top: 4px;
      background-size: 328px 328px;
      &.animated {
        opacity: 0;
        transform: translateY(100%) scale(2.0);
        animation: fadeScaleIn 1s ease-in-out 700ms forwards;
      }
    }
    .i-white__circle {
      .middle;
      background: url("./assets/img/white_circle.png") left center no-repeat;
      width: 330px;
      height: 330px;
      margin-top: 4px;
      background-size: 315px 330px;
      &.animated {
        opacity: 0;
        transform: translateY(100%) scale(2.0);
        animation: fadeScaleIn 1s ease-in-out 800ms forwards, rotate360 15s linear 2s infinite;
      }
    }
    .i-lightblue__circle {
      .middle;
      background: url("./assets/img/lightblue_circle.png") center bottom no-repeat;
      width: 300px;
      height: 300px;
      background-size: 266px 86px;
      &.animated {
        opacity: 0;
        transform: translateY(100%) scale(2.0);
        animation: fadeScaleIn 1s ease-in-out 800ms forwards, rotate360R 10s linear 2s infinite;
      }
    }
    .i-gray__circle {
      .middle;
      background: url("./assets/img/gray_circle.png") center center no-repeat;
      width: 246px;
      height: 246px;
      margin-top: 3px;
      background-size: 100% 100%;
      &.animated {
        opacity: 0;
        transform: translateY(100%) scale(2.0);
        animation: fadeScaleIn 1s ease-in-out 1s forwards;
      }
    }
  }
  @keyframes fadeIn {
    0%{opacity: 0;}
    100%{opacity:1;}
  }
  @keyframes rollIn {
    0%{opacity:0;transform:scale(0);}
    100%{opacity:1;transform:scale(1);}
  }
  @keyframes fadeScaleIn {
    0%{
      opacity: 0;
      transform: translateY(100%) scale(2.0);
    }
    100%{
      opacity: 1;
      transform: translateY(-50%) scale(1.0);
    }
  }
  @keyframes fadeTranslateIn {
    0%{opacity: 0;transform: translateY(-100%);}
    100%{opacity: 1;transform: translateY(0);}
  }
  @keyframes fadeIn {
    0%{opacity:0;}
    100%{opacity:1;}
  }
  /** animation */
  @keyframes rotate360 {
    0%{transform: translateY(-50%) rotate(0)}
    100%{transform: translateY(-50%) rotate(360deg)}
  }
  @keyframes rotate360R {
    0%{transform: translateY(-50%) rotate(360deg)}
    100%{transform: translateY(-50%) rotate(0)}
  }
  @keyframes rotate45 {
    from{transform: translateY(-50%) rotate(0)}
    to{transform: translateY(-50%) rotate(45deg)}
  }
  .rotate360(@duration, @name, @iteration:infinite, @timing: linear) {
    animation-name: @name;
    animation-timing-function: @timing;
    animation-fill-mode: both;
    animation-duration: @duration;
    animation-iteration-count: @iteration;
  }
  .rotate45 {
    .rotate360(500ms, rotate45, normal, ease-out);
  }
  .rotate36 {
    .rotate360(2s, rotate360, infinite, linear);
  }
</style>



