<template>
  <div id="login">
    <div class="i-form">
      <div class="i-form__item">
        <i class="i-icon i-icon-user"></i>
        <span>用户名</span>
        <input class="i-input i-input__sample" title="" type="text" @focus="focus" @blur="blur">
      </div>
      <div class="i-form__item">
        <i class="i-icon i-icon-lock"></i>
        <span>密码</span>
        <input class="i-input i-input__sample" title="" type="password" @focus="focus" @blur="blur">
      </div>
      <div class="i-form__submit">
        <a href="javascript:void(0);" @click="login">
          <img alt="login" src="../assets/img/login.png">
        </a>
      </div>
    </div>
    <audio class="i-audio" controls :src="src"></audio>
  </div>
</template>

<script>
  import src from '../assets/audio/welcome.wav'
  import { toolMixins } from '../mixins/tools'

  export default {
    name: 'login',
    mixins: [toolMixins],
    methods: {
      focus (event) {
        let { parent, grayCircle } = {
          parent: event.target.parentNode,
          grayCircle: document.querySelector('#grayCircle')
        }
        this.addClass(parent, 'active')
        this.addClass(grayCircle, 'rotate45')
      },
      blur (event) {
        let { parent, grayCircle } = {
          parent: event.target.parentNode,
          grayCircle: document.querySelector('#grayCircle')
        }
        this.removeClass(parent, 'active')
        this.removeClass(grayCircle, 'rotate45')
      },
      login () {
        let grayCircle = document.querySelector('#grayCircle')
        let _this = this
        this.addClass(grayCircle, 'rotate36')
        setTimeout(function () {
          _this.$router.push({
            path: 'index'
          })
        }, 3 * 1000)
      }
    },
    mounted () {
      let {audio, circle} = {
        audio: document.querySelector('.i-audio'),
        circle: document.querySelector('#circle')
      }
      this.removeClass(circle, 'paused')
      this.addClass(circle, 'animated')
      setTimeout(function () {
        audio.play()
      }, 2000)
    },
    data () {
      return {
        src: src
      }
    }
  }
</script>

<style lang="less">
  @import "../assets/css/base";

  audio.i-audio {
    position: absolute;
    width: 0;
    height: 0;
    display: none;
  }
  .i-form {
    .middle;
    z-index: 101;
    width: 160px;
    height: 160px;
    .i-form__item {
      position: relative;
      margin-top: 20px;
      opacity: 0;
      transform: translateY(-100%);
      animation: fadeTranslateIn 1s ease-in-out 0.5s forwards;
      &:after {
        content: '';
        height: 1px;
        margin-top: -1px;
        position: absolute;
        left: 0;
        width: 0;
        bottom: 0;
        transition: all .5s;
        box-shadow: 0 0 2px #00f3f3, 0 0 5px #00f3f3, 0 0 10px #00f3f3;
      }
      &:first-child {
        margin-top: 25px;
      }
      &:nth-child(2) {
        animation-delay: 1s;
      }
      &.active {
        &:after {
          width: 162px;
        }
        > i {
          transform: scale(0.65);
          top: -15px;
        }
        > span {
          width: 80px;
          margin-left: 20px;
        }
        .i-input {
          text-indent: 10px;
        }
      }
      > i {
        position: absolute;
        top: 5px;
        transform-origin: 0 0;
        transition: all .5s;
      }
      > span {
        position: absolute;
        color: #00f3f3;
        margin-left: 30px;
        top: -15px;
        font-size: 12px;
        width: 0;
        overflow: hidden;
        transition: all .5s;
        white-space: nowrap;
      }
      > .i-input {
        line-height: 32px;
        height: 32px;
        width: 160px;
        color: #00f3f3;
        text-indent: 32px;
        font-size: 18px;
        transition: all .5s;
        position: relative;
      }
    }
    .i-form__submit {
      text-align: center;
      margin-top: 20px;
      opacity: 0;
      transform: translateY(-100%);
      animation: fadeTranslateIn 1s ease-in-out 1.5s forwards;
      > a {
        display: inline-block;
        cursor: pointer;
        position: relative;
        &:before, &:after {
          transition: all .5s;
          position: absolute;
          content: '';
          width: 18px;
          height: 19px;
          left: 8px;
          top: -1px;
          transform: skewX(45deg);
        }
        &:after {
          top: 20px;
          transform: skewX(-45deg);
        }
        &:hover:before, &:hover:after {
          box-shadow: 0 0 5px #00f3f3, 0 0 20px #00f3f3, 0 0 30px #00f3f3, 0 0 50px #00f3f3;
        }
      }
    }
  }
</style>



